import * as Yup from 'yup';
export var usernameSchema = Yup.object().shape({
    name: Yup.string()
        .required('usernameNotFilled'),
});
export var emailSchema = Yup.object().shape({
    email: Yup.string()
        .email('    ')
        .matches(/\./, '    ')
        .required('emailNotFilled'),
});
export var passwordSchema = Yup.object().shape({
    password: Yup.string()
        .min(8, '    8-15 ')
        .max(15, '    8-15 ')
        .matches(/[a-z]/, '    1  ')
        .matches(/[A-Z]/, '    1  ')
        .matches(/\d/, '    1 ')
        .matches(/[!@#$%^&*()_=+-]/, '    1   (!,@,#,$...)')
        .matches(/^[aA-zZ\d!@#$%^&*()_=+-]+$/, '    :      ,   ')
        .required('passwordNotFilled'),
});
export var confirmPasswordSchema = Yup.object().shape({
    confirmPassword: Yup.string()
        .required('confirmPasswordNotFilled')
        .oneOf([Yup.ref('password'), null], '  '),
});
